/* open-sans-regular - latin */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), local("Open-Sans"), url("../fonts/open-sans-v18-latin-regular.woff2") format("woff2"), url("../fonts/open-sans-v18-latin-regular.woff") format("woff"); }

/* open-sans-700 - latin */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"), local("Open-Sans"), url("../fonts/open-sans-v18-latin-700.woff2") format("woff2"), url("../fonts/open-sans-v18-latin-700.woff") format("woff"); }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: inherit; }

*:focus {
  outline-style: groove; }

body {
  position: relative;
  overflow-x: hidden;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.35;
  font-weight: 400;
  letter-spacing: normal;
  color: #363636; }

section {
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.container {
  position: relative;
  width: auto;
  min-width: 1024px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 50px; }
  @media (min-width: 720px) and (max-width: 1023px) {
    .container {
      min-width: 720px;
      width: auto;
      padding: 0 25px;
      overflow-x: hidden; } }
  @media (min-width: 0) and (max-width: 720px) {
    .container {
      min-width: 320px;
      width: 100%;
      padding: 0 15px;
      overflow-x: hidden; } }

.js-block {
  display: block; }

.js-hide {
  display: none; }

.btn {
  display: inline-block;
  vertical-align: middle;
  width: max-content;
  margin-left: -22px;
  padding: 11px 32px 13px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 700;
  letter-spacing: -0.3px;
  color: #363636;
  border: 1px solid #d8d8d8;
  border-radius: 25px;
  cursor: pointer; }
  .btn:hover, .btn:focus {
    outline: 0 none;
    opacity: 0.8;
    border: 1px solid #363636; }
  .btn:active {
    outline: 0 none;
    opacity: 0.6;
    border: 1px solid #363636; }

.btn--icon {
  padding-right: 28px; }

.btn--icon span {
  position: relative;
  padding-right: 17px; }

.btn--icon span::after {
  content: "";
  position: absolute;
  right: 0;
  top: 4px;
  display: block;
  width: 10px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: 50% 50%; }
  @media (min-width: 0) and (max-width: 720px) {
    .btn--icon span::after {
      width: 9px;
      height: 17px; } }

.btn--arrow span::after {
  background-size: 10px 18px;
  background-image: url("../img/chevron.svg"); }

.card {
  --half-gap: 10px;
  width: calc(379px + (2 * var(--half-gap)));
  padding-left: var(--half-gap);
  padding-right: var(--half-gap); }

.card a {
  display: block;
  color: #363636;
  border: 1px solid #d8d8d8;
  background-color: #f8f8f8; }

.card__img-wrapper {
  position: relative; }

.card__img-wrapper img {
  width: 100%;
  height: auto; }

.card .card__img-wrapper > span {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 100;
  display: block;
  padding: 6px 11px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 1.35;
  font-weight: 700;
  color: white; }

.card--independent .card__img-wrapper > span {
  background-color: #006f79; }

.card--support .card__img-wrapper > span {
  background-color: #ec6608; }

.card__info-wrapper {
  padding: 20px 20px 14px; }

.card__info-wrapper h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 1.36;
  font-weight: 700; }

.card__info-wrapper h2 + p {
  margin-top: 5px; }

.card__info-wrapper p:last-of-type {
  margin-bottom: 5px; }

.card small {
  margin-top: 0;
  font-size: 14px; }

.houses__container {
  padding-top: 35px;
  padding-bottom: 100px; }

.houses h1 {
  margin-top: 0;
  margin-left: -22px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 36px;
  line-height: 1.36;
  font-weight: 700;
  color: #45852d;
  text-align: center; }
  @media (min-width: 0) and (max-width: 720px) {
    .houses h1 {
      margin-left: 0;
      font-size: 28px; } }

.houses__filter {
  width: 474px;
  margin-top: 47px;
  margin-bottom: 47px; }
  @media (min-width: 720px) and (max-width: 1023px) {
    .houses__filter {
      width: 100%; } }
  @media (min-width: 0) and (max-width: 720px) {
    .houses__filter {
      width: 100%; } }

.houses__cards-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  justify-content: flex-start;
  align-items: stretch;
  margin-left: -10px;
  margin-right: -10px; }

.houses__cards-wrapper article {
  width: calc(100% / 3);
  margin-bottom: 38px; }
  @media (min-width: 720px) and (max-width: 1023px) {
    .houses__cards-wrapper article {
      width: calc(100% / 2); } }
  @media (min-width: 0) and (max-width: 720px) {
    .houses__cards-wrapper article {
      width: 100%;
      max-width: 500px;
      margin-left: auto;
      margin-right: auto; } }

.houses__see-more {
  margin-top: 23px;
  text-align: center; }

.filter > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.filter label {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 700;
  margin-right: 10px; }

.filter input[type="search"] {
  flex-grow: 1;
  width: auto;
  padding: 13px 18px 12px;
  border: 1px solid #d8d8d8;
  border-radius: 25px; }
  .filter input[type="search"]:focus {
    outline: 0 none;
    border: 1px solid #363636; }
  .filter input[type="search"]:active {
    outline: 0 none;
    border: 1px solid #363636; }
